.logo {
    position: absolute;
    width: 25%;
    top: 1.5em;
    left: 2em;
    z-index: -9999;
}
@media screen and (min-width: 768px) {
    .logo {
        width: 18.75%;
        top: 1.5em;
        left: 2.5em;
    }
}
@media screen and (min-width: 800px) {
    .logo {
        width: 22.5%;
        top: 2em;
        left: 3.5em;
    }
}
@media screen and (min-width: 960px) {
    .logo {
        width: 10%;
        top: 1.5em;
        left: 2.5em;
    }
}
@media screen and (min-width: 1280px) {
    .logo {
        width: 8.25%;
        top: 1.75em;
        left: 3.5em;
    }
}
@media screen and (min-width: 1280px) and (min-height: 880px) {
    .logo { width: 10%; }
}
@media screen and (min-width: 1440px) {
    .logo {
        width: 8.5%;
        top: 2.5em;
        left: 5em;
    }
}
@media screen and (min-width: 1920px) {
    .logo {
        width: 7%;
        top: 2.5em;
        left: 5em;
    }
}