.under svg {
    width: 100%;
}
.under svg:first-child {
    width: 95%;
}
.stripes {
    position: absolute;
    right: -25%;
    top: 10%;
    transform: rotate(37.5deg);
    z-index: -1;
}
.caution {
    position: absolute;
    right: -22.5%;
    top: 6.25%;
    transform: rotate(20deg);
    z-index: -2;
}
.caution2 {
    position: absolute;
    right: -20%;
    top: 3.75%;
    transform: rotate(13.25deg);
    z-index: -3;
}
@media screen and (min-width: 375px) and (min-height: 810px) {
    .caution {
        transform: rotate(21.25deg);
    }
    .caution2 {
        right: -17.5%;
        transform: rotate(15deg);
    }
}
@media screen and (min-width: 768px) and (min-height: 1024px) {
    .under svg {
        width: 90%;
    }
    .under svg:first-child {
        width: 85%;
    }
    .stripes {
        right: -20%;
        top: 12.5%;
    }
    .caution {
        transform: rotate(26.25deg);
    }
    .caution2 {
        right: -17.5%;
        transform: rotate(13.75deg);
    }
}

@media screen and (min-width: 960px) {
    .under svg {
        width: 50%;
    }
    .under svg:first-child {
        width: 45%;
    }
    .stripes {
        right: -5%;
    }
    .caution {
        right: -2.5%;
        top: 5.5%;
        transform: rotate(20deg);
    }
    .caution2 {
        right: -4%;
        top: 4%;
        transform: rotate(12.5deg);
    }
}
@media screen and (min-width: 1280px) {
    .under svg {
        width: 35%;
    }
    .under svg:first-child {
        width: 30%;
    }
    .stripes {
        right: -5%;
    }
    .caution {
        right: -2.5%;
        top: 5.5%;
        transform: rotate(20deg);
    }
    .caution2 {
        right: -4%;
        top: 4%;
        transform: rotate(12.5deg);
    }
}
@media screen and (min-width: 1280px) and (min-height: 880px) {
    .caution2 {
        right: -2%;
        top: 3.5%;
        transform: rotate(13.75deg);
    }
}