.countdownContainer {
    position: absolute;
    top: 27.5%;
    left: 50%;
    transform: translate(-50%);
}
.countdownContainer h2 {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
    font-size: 1.25em;
}
.countdown {
	display: grid;
	grid-template-columns: repeat(7, auto);
	justify-items: center;
	align-items: center;
	cursor: wait;
	/* border-top: 1px solid white; */
}
.countdown hr {
	height: 62.5%;
	border: none;
	border-right: 1px solid white;
}
.countdown h1 {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 2em;
	padding: 0.25em .5em;
}
.countdown h1 span {
	font-size: 0.375em;
	height: 100%;
	margin-top: 0.375em;
}

@media screen and (min-width: 375px) {
    .countdown h1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 2em;
        padding: 0.25em .625em;
    }
}
@media screen and (min-width: 375px) and (min-height: 810px) {
    .countdownContainer {
        top: 22.5%;
    }
}
@media screen and (min-width: 480px) {
    .countdownContainer {
        top: 27.5%;
    }
    .countdown h1 {
        font-size: 2.375em;
        padding: 0.25em .675em;
    }
}
@media screen and (min-width: 600px) {
    .countdown h1 {
        font-size: 2.625em;
        padding: 0.5em .675em;
    }
    .countdownContainer h2 {
        font-size: 1.5em;
    }
}
@media screen and (min-width: 768px) and (min-height: 1024px) {
    .countdownContainer {
        top: 23.75%;
    }
    .countdown h1 {
        font-size: 3.125em;
        padding: 0.5em .675em;
    }
    .countdownContainer h2 {
        font-size: 1.75em;
    }
}
@media screen and (min-width: 800px) and (min-height: 1280px) {
    .countdown h1 {
        font-size: 3.5em;
        padding: 0.5em .75em;
    }
    .countdownContainer h2 {
        font-size: 2.25em;
    }
}
@media screen and (min-width: 960px) {
    .countdownContainer {
