.menu-news.index {
    z-index: 9999;
}
.menu-news {
	position: absolute;
	bottom: 2.5%;
	left: 12.5%;
    z-index: -1;
}
.news-icon {
	font-size: 1.75em;
}
.news-icon2 {
	display: none;
}
.menu-news figure {
    z-index: -9999;
}
.menu-news figure p {
    display: none;
}
.menu-news aside{
	background: rgb(105, 105, 105, 0.25);
	backdrop-filter: blur(.5em);
	width: 100%;
	height: 100vh;
	position: fixed;
	top: 0;
	left: -100%;
	transition: .75s;
}
.menu-news aside.active {
    left: 0;
}

/* hide */
.menu-news .x.hide path {
	color: transparent;
	transition: .375s;
}
/* top */
.top {
    width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: absolute;
    padding: 2.5% 5% 0;
}
.top p {
    font-size: 1.125em;
    display: flex;
    justify-content: center;
    align-items: center;
}
.top p svg {
    margin-left: .375em;
    font-size: 1.125em;
}
.menu-news .x {
	font-size: 2em;
	cursor: pointer;
    transition: 1s;
}
.menu-news .x path {
    transition: .375s;
}
/* articles */
.section-api {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30% 5% 0;
}
.section-api * {
    font-weight: 100;
}
.menu-news aside {
    overflow-y: scroll;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.menu-news aside::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
.menu-news aside {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.section-api article {
    margin-bottom: 10%;
}
.section-api p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: .875em;
    text-align: justify;
    margin: 2.5% 0;
}
.section-api h5 {
    text-align: right;
}
.section-api article div {
    background: rgb(230, 57, 9, 1);
    /* background: rgb(0, 0, 0, .5); */
    height: .5em;
    width: 50%;
    margin-bottom: .5em;
    border-radius: .125em;
}
/* 480 */
@media screen and (min-width: 480px) {
    .news-icon {
        font-size: 2em;
    }
    .section-api {
        padding: 30% 6.25% 0;
    }
    .section-api h3 {
        font-size: 1.5em;
    }
    .section-api h5 {
        font-size: .875em;
    }
    .section-api p {
        font-size: 1em;
    }
    .top p {
        font-size: 1.25em;
    }
}
/* 600 */
@media screen and (min-width: 600px) {
    .news-icon {
        font-size: 2.25em;
    }
    .section-api h3 {
        font-size: 1.75em;
    }
    .section-api h5 {
        font-size: 1em;
    }
    .section-api p {
        font-size: 1.25em;
    }
    .top p {
        font-size: 1.5em;
    }
    .menu-news .x {
        font-size: 2.25em;
    }
}
/* 768 */
@media screen and (min-width: 768px) {
    .news-icon {
        font-size: 2.5em;
    }
    .menu-news aside{
        width: 87.5%;
        left: -87.5%;
    }
    .section-api {
        padding: 30% 5% 0;
    }
}
/* 800 */
@media screen and (min-width: 800px) {
    .news-icon {
        font-size: 2.75em;
    }
    .menu-news .x {
        font-size: 2.625em;
    }
}
/* desktop */
@media screen and (min-width: 960px) {
    .menu-news {
        bottom: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        top: 50%;
        left: 5%;
        user-select: none;
        z-index: 9999;
    }
    .news-icon {
        font-size: 1.5em;
        cursor: pointer;
        transition: 0.75s;
    }
    .news-icon2 {
        display: inline-block;
        position: absolute;
        left: 0;
        opacity: 0;
    }
    .menu-news figure:hover .news-icon2 {
        opacity: 1;
    }
    .menu-news figure p {
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin-top: .25em;
        transition: .375s;
        pointer-events: none;
        font-size: .825em;
    }
    .menu-news figure:hover .news-icon1,
    .menu-news figure:hover p,
    .menu-news figure p.fade {
        opacity: 0;
    }
    .menu-news aside{
        width: 22.5%;
        left: -22.5%;
    }
    /* top */
    .top {
        user-select: none;
    }
    .top p {
        font-size: 1em;
    }
    .top p svg {
        font-size: 1.125em;
    }
    .menu-news .x {
        font-size: 1.75em;
    }
    .menu-news .x:hover path {
        color: rgba(255, 255, 255, .325);
    }
    /* articles */
    .section-api {
        padding: 35% 5% 0;
    }
    .section-api h3 {
        font-size: 1.125em;
    }
    .section-api h5 {
        text-align: right;
        font-size: .75em;
    }
    .section-api p {
        font-size: .825em;
    }
    .section-api article div {
        height: .375em;
    }
}
/* 1280 */
@media screen and (min-width: 1280px) {
    .news-icon {
        font-size: 1.75em;
    }
}
/* 1400 */
@media screen and (min-width: 1400px) {
    .news-icon {
        font-size: 2em;
    }
}
/* 1440 */
@media screen and (min-width: 1440px) {
    .news-icon {
        font-size: 1.875em;
    }
}
/* 1536 */
@media screen and (min-width: 1536px) {
    .news-icon {
        font-size: 1.875em;
    }
    .menu-news figure p {
        margin-top: .5em;
    }
}
@media screen and (min-width: 1920px) {
    .menu-news {
        left: 3.5%;
    }
    .menu-news aside{
        width: 18.75%;
        left: -18.75%;
    }
    .section-api h3 {
        font-size: 1.25em;
    }
    .section-api h5 {
        font-size: .825em;
    }
    .section-api p {
        font-size: .875em;
    }
}