.construction {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 25%;
    user-select: none;
}
.construction h1 {
    font-size: 2.25em;
    margin: .75em 0 .5em;
    text-align: center;
}
.construction h3 {
    font-weight: 100;
}
.construction svg {
    font-size: 2.75em;
    fill: #ff7300;
}
@media screen and (min-width: 960px) {
    .construction h1 {
        font-size: 1.75em;
    }
    .construction h3 {
        font-size: 1em;
    }
}