.tab-container {
	position: absolute;
	left: 5%;
	top: 60%;
	/*  */
	width: 90%;
	display: flex;
	flex-direction: column;
    /* background: #a52a2a40; */
/*  */
    /* display: none; */
    z-index: -9999;
}

/* tabs */
.tab-menu {
	display: flex;
    position: relative;
    width: fit-content;
}
.tab-menu::after {
    content: '';
	background: rgb(255, 255, 255, .25);
	width: 102.5%;
	position: absolute;
	bottom: -50%;
    left: 50%;
	transform: translateX(-50%);
    z-index: -1;
    height: 2px;
    display: flex;
    justify-content: center;
}
.tabs {
	position: relative;
	display: flex;
}
.tabs a {
    transition: .5s;
}
.tabs a:hover {
    color: rgba(255, 255, 255, 0.5);
}
.tabs-div {
    display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 .75em;
}
.tab-menu a:first-child div {
    padding-left: 0;
}
.tab-menu a:last-child div {
    padding-right: 0;
}
.tabs::after {
    content: '';
	background: white;
	height: 2px;
	width: 125%;
	position: absolute;
	bottom: -50%;
    display: flex;
	/* center to h3 */
	left: 50%;
	transform: translateX(-50%);
    opacity: 0;
    transition: .5s;
}
.tabs-div-active .tabs::after {
    opacity: 1;
}

/* content */
.tab-content {
	margin-top: 2.5%;
}
.content {
	display: none;
}
.content-active {
	display: grid;
	grid-template-columns: auto;
	grid-template-rows: repeat(3, auto);
}
/* font */
.tab-container * {
    line-height: 100%;
}
.tab-container h3 {
    font-size: 0.8275em;
	font-weight: 100;
    cursor: pointer;
}
.tab-container h1 {
    font-family: bourgeois;
	font-size: 5em;
	margin-left: -1%;
    line-height: 75%;
    margin-top: .125em;
}
.tab-container h2 {
    font-size: 1.5em;
	font-weight: 100;
    margin-top: .75em;
}
.tab-container p {
    display: none;
}

/* MEDIA QUERIES */
