@font-face {
	font-family: foundry;
	src: url(files/FoundryGridnik-Regular.otf);
}
@font-face {
	font-family: foundry-bold;
	src: url(files/FoundryGridnik-Bold.otf);
}
@font-face {
	font-family: bourgeois;
	src: url(files/BourgeoisBook.otf);
}
* {
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
	margin: 0;
	padding: 0;
	font-family: foundry;
    color: white;
}
#App {
	width: 100vw;
    height: 100vh;
    background: #1d1c1b;
    background-image: url('files/base.jpg');
	background-repeat: no-repeat;
	background-position: 25% center;
	position: fixed;
    background-size: cover;
	background-attachment: fixed;
    overflow: hidden;
    z-index: -9999;
}
#App.mission {
    background-image: url('files/mission.jpg');
    background-position: 80% center;
}
#App.rocket {
    background-image: url('files/rocket.jpg');
    background-position: 62.5% center;
}
#App.mars {
    background-image: url('files/mars.jpg');
    background-position: center;
}

/* MEDIA QUERIES */
@media screen and (min-width: 768px) {
    #App.rocket {
        background-position: 55% center;
    }
    #App.mission {
        background-image: url('files/mission.jpg');
        background-position: 75% center;
    }
}
@media screen and (min-width: 800px) {
    #App.rocket {
        background-position: 60% center;
    }
    #App.mission {
        background-image: url('files/mission.jpg');
        background-position: 75% center;
    }
}
@media screen and (min-width: 960px) {
    #App {
        background-position: 37.5% bottom;
        transition: 1s;
    }
    #App.mission {
        background-position: center 75%;
    }
    #App.rocket {
        background-position: center bottom bottom;
    }
    #App.mars {
        background-position: center top;
    }
}
@media screen and (min-width: 1920px) {
    #App {
        background-position: center bottom;
    }
    #App.mission {
        background-position: center 75%;
    }
    #App.rocket {
        background-position: center bottom bottom;
    }
    #App.mars {
        background-position: center top;
}
}