.menu-nav.index {
    z-index: 9999;
}
.menu-nav {
    position: absolute;
	bottom: 1.5%;
    right: 12.5%;
    z-index: -1;
}
.info-icon {
    font-size: 2.25em;
}
.info-icon2 {
    display: none;
}
.menu-nav figure p {
    display: none;
}
.nav-menu {
    background: rgb(105, 105, 105, .25);
    backdrop-filter: blur(.5em);
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -100%;
    transition: .75s;
}
.nav-menu.active {
    right: 0;
}
.nav-x {
    font-size: 2em;
    position: absolute;
    top: 1%;
    left: 5%;
    cursor: pointer;
    transition: 1s;
}
/* hide */
.menu-nav .nav-x.hide path {
    color: transparent;
    transition: .375s;
}
.nav-x path {
    transition: .375s;    
}
.nav-x:hover path {
    color: rgba(255, 255, 255, .325);  
}
/* 480 */
@media screen and (min-width: 480px) {
    .info-icon {
        font-size: 2.5em;
    }
}
/* 600 */
@media screen and (min-width: 600px) {
    .info-icon {
        font-size: 2.75em;
    }
    .nav-x {
        font-size: 2.25em;
    }
}
/* 768 */
@media screen and (min-width: 768px) {
    .info-icon {
        font-size: 3.125em;
    }
    .nav-menu {
        width: 87.5%;
        right: -87.5%;
    }
}
/* 800 */
@media screen and (min-width: 800px) {
    .info-icon {
        font-size: 3.5em;
    }
    .nav-x {
        font-size: 2.625em;
        top: .75%;
    }
}
/* desktop */
@media screen and (min-width: 960px) {
    .menu-nav {
        z-index: 9999;
        bottom: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        top: 50%;
        right: 5%;
        user-select: none;
    }
    .info-icon {
        font-size: 2em;
        cursor: pointer;
        transition: 0.75s;
    }
    .info-icon2 {
        display: inline-block;
        position: absolute;
        left: 0;
        opacity: 0;
    }
    .menu-nav figure:hover .info-icon2 {
        opacity: 1;
    }
    .menu-nav figure p {
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 0;
        transition: .375s;
        pointer-events: none;
        font-size: .825em;
    }
    .menu-nav figure:hover .info-icon1,
    .menu-nav figure:hover p,
    .menu-nav figure p.fade {
        opacity: 0;
    }
    .menu-nav nav{
        width: 27.5%;
        right: -27.5%;
    }
    .nav-x {
        font-size: 1.75em;
        top: .75%;
    }
}
/* 1280 */
@media screen and (min-width: 1280px) {
    .info-icon {
        font-size: 2.325em;
    }
}
/* 1400 */
@media screen and (min-width: 1400px) {
    .info-icon {
        font-size: 2.625em;
    }
}
/* 1440 */
@media screen and (min-width: 1440px) {
    .info-icon {
        font-size: 2.5em;
    }
}
/* 1536 */
@media screen and (min-width: 1536px) {
    .info-icon {
        font-size: 2.5em;
    }
}



/* desktop */
@media screen and (min-width: 1920px) {
    .menu-nav {
        position: absolute;
        top: 50%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        right: 3.75em;
        z-index: 9999;
    /*  */
        /* display: none; */
    }
    .info-icon {
        font-size: 2.75em;
        cursor: pointer;
        transition: .75s;
    }
    .info-icon2 {
        position: absolute;
        right: 0;
        opacity: 0;
    }
    .menu-nav figure p {
        margin-top: .125em;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        transition: .375s;
        pointer-events: none;
    }
    .menu-nav figure:hover .info-icon2 {
        opacity: 1;
    }
    .menu-nav figure:hover .info-icon1,
    .menu-nav figure:hover p,
    .menu-nav figure p.fade {
        opacity: 0;
    }
    .nav-menu {
        background: rgb(105, 105, 105, .25);
        backdrop-filter: blur(.5em);
        width: 25em;
        height: 100vh;
        position: fixed;
        transition: .75s;
        top: 0;
        right: -25em;
    }
    .nav-menu.active {
        right: 0;
        transition: .5s;
    }
    .nav-x {
        font-size: 2em;
        position: absolute;
        top: .125em;
        left: .125em;
        cursor: pointer;
    }
    /* hide */
    .nav-x.hide path {
        color: transparent;
        transition: .375s;
    }
    .nav-xshow path {
        color: white;
        transition: 1s;
    }
    .nav-x path {
        transition: .375s;    
    }
    .nav-x:hover path {
        color: rgba(255, 255, 255, .325);  
    }
}